<template>
  <v-container>
  <v-row>
      <v-col cols="1"></v-col>
      <v-col cols="10">
        <v-container fluid>

        <v-row justify="center">

       <v-col
        class="d-flex"
        cols="12"
        sm="6"
      >
       <v-select
          return-object
          :items="pollingUnits"
          v-model="pollingUnit"
          name="pollingUnit"
           item-text="polling_unit_name"
           item-value="polling_unit_number"
          label="Select polling unit..."
          @change="getPollingUnitResult(pollingUnit)"
        ></v-select>
      </v-col>
            <template>
    <v-data-table
      :headers="headers"
      :items="pollingUnitResults"
      :items-per-page="5"
      class="elevation-1"
    ></v-data-table>
  </template>
  </v-row>
        </v-container>
      </v-col>
      <v-col cols="1"></v-col>
    </v-row>
     </v-container>
</template>

<script>
import axios from 'axios'

export default {
    data () {
      return {
        pollingUnit: '',
        pollingUnits: {},
        headers: [
        {
          text: 'Result ID',
          align: 'start',
          sortable: false,
          value: 'result_id',
        },
        { text: 'Party', value: 'party_abbreviation' },
        { text: 'Score', value: 'party_score' },
        { text: 'Entered by', value: 'entered_by_user' },
        { text: 'Ip Address', value: 'user_ip_address' },
      ],
      pollingUnitResults: [],
      }
    },

    methods: {
      getAllPollingUnits(){
        axios.get('/api/all-polling-units')
        .then(result => {
          this.pollingUnits = result.data
        })
        .catch(err => {
          console.log(err)
        })
      },

      getPollingUnitResult(pollingUnit) {
        axios.get(`/api/pol-unit-result/${pollingUnit.polling_unit_number}`)
        .then(result => {
          this.pollingUnitResults = result.data
        })
        .catch(err => {
          console.log(err)
        })

      }
    },

    mounted() {
      this.getAllPollingUnits()
    },

    computed:{
      options(){
          let opts = [];
          for(let i = 1; i <= this.pollingUnits; i++){
              opts.push({text:i, value:i});
          }
          return opts
      }
    }
  }
</script>
