<template>
  <v-container>
  <v-row>
      <v-col cols="1"></v-col>
      <v-col cols="10">
        <v-container fluid>

        <v-row justify="center">
        <form @submit.prevent="submitResult">
          <v-select
            return-object
            :items="pollingUnits"
            v-model="pollingUnit"
            name="pollingUnit"
            item-text="polling_unit_name"
            item-value="polling_unit_uniqueid"
            label="Select polling unit..."
          ></v-select>
          <v-select
            return-object
            :items="parties"
            v-model="party"
            name="party"
            item-text="partyname"
            item-value="partyid"
            label="Select party..."
          ></v-select>
            <v-text-field
              v-model="score"
              :counter="10"
              label="Enter score..."
              required
            ></v-text-field>
            <v-text-field
              v-model="entered_by_user"
              :counter="10"
              label="Enter by?"
              required
            ></v-text-field>


          <v-btn
            class="mr-4"
            type="submit"
          >
            submit
          </v-btn>
        </form>
  </v-row>
        </v-container>
      </v-col>
      <v-col cols="1"></v-col>
    </v-row>
     </v-container>
</template>

<script>
import axios from 'axios'

export default {
    data () {
      return {
        pollingUnits: {},
        parties: {},
        pollingUnit: '',
        party: '',
        score: '',
        entered_by_user: '',
        data: '',
      }
    },

    methods: {
      getAllPollingUnits(){
        axios.get('/api/all-polling-units')
        .then(result => {
          this.pollingUnits = result.data
        })
        .catch(err => {
          console.log(err)
        })
      },

      getAllParties(){
        axios.get('/api/all-parties')
        .then(result => {
          this.parties = result.data
        })
        .catch(err => {
          console.log(err)
        })
      },

      submitResult(){
        this.data = {
            pollingUnitUniqueid: this.pollingUnit.uniqueid,
            party_abbreviation: this.party.partyname,
            party_score: this.score,
            entered_by_user: this.entered_by_user
        }

        axios.post('/api/add-result', this.data)
        .then(result => {
            console.log(result.data)
        })
        .catch(err => {
          console.log(err)
        })

      }
    },

    mounted() {
      this.getAllPollingUnits()
      this.getAllParties()
    }
  }
</script>
