<template>
  <v-container>
  <v-row>
      <v-col cols="1"></v-col>
      <v-col cols="10">
        <v-container fluid>

        <v-row justify="center">

       <v-col
        class="d-flex"
        cols="12"
        sm="6"
      >
       <v-select
          return-object
          :items="LGAs"
          v-model="localGov"
          name="localGov"
           item-text="lga_name"
           item-value="lga_id"
          label="Select LGA..."
          @change="getLGAPolUnitRes(localGov)"
        ></v-select>

      <h1>Total: <span v-html="total"></span></h1>
      </v-col>
  </v-row>
        </v-container>
      </v-col>
      <v-col cols="1"></v-col>
    </v-row>
     </v-container>
</template>

<script>
import axios from 'axios'

export default {
    data () {
      return {
        localGov: '',
        LGAs: {},
        headers: [
        {
          text: 'Result ID',
          align: 'start',
          sortable: false,
          value: 'result_id',
        },
        { text: 'Party', value: 'party_abbreviation' },
        { text: 'Score', value: 'party_score' },
        { text: 'Entered by', value: 'entered_by_user' },
        { text: 'Ip Address', value: 'user_ip_address' },
      ],
      total:'',
      }
    },

    methods: {
      getAllLGA(){
        axios.get('/api/all-lga')
        .then(result => {
          this.LGAs = result.data
        })
        .catch(err => {
          console.log(err)
        })
      },

      getLGAPolUnitRes(localGov) {
        axios.get(`/api/total-result/${localGov.lga_id}`)
        .then(result => {
          this.total = result.data.count
        })
        .catch(err => {
          console.log(err)
        })

      }
    },

    mounted() {
      this.getAllLGA()
    }
  }
</script>
